import React, { useEffect, useState } from 'react';
import {useParams} from "react-router"
import { Link as RouterLink, useLocation } from 'react-router-dom';
import PropTypes from 'prop-types';
import {
  Avatar,
  Box,
  Button,
  Divider,
  Drawer,
  Hidden,
  List,
  Typography,
  makeStyles
} from '@material-ui/core';
import {
  AlertCircle as AlertCircleIcon,
  BarChart as BarChartIcon,
  Lock as LockIcon,
  Settings as SettingsIcon,
  ShoppingBag as ShoppingBagIcon,
  User as UserIcon,
  UserPlus as UserPlusIcon,
  Users as UsersIcon
} from 'react-feather';
import NavItem from './DepartmentItem';

const user = {
  avatar: '/static/images/avatars/avatar_6.png',
  jobTitle: 'Senior Developer',
  name: 'Katarina Smith'
};

const useStyles = makeStyles(() => ({
  mobileDrawer: {
    width: 256
  },
  desktopDrawer: {
    width: 256,
    top: 64,
    height: 'calc(100% - 64px)',
    zIndex: 0
  },
  avatar: {
    cursor: 'pointer',
    width: 64,
    height: 64
  },
  Container: {
    width:'256px',
    display: 'inline-block',
  }
}));

function ListBar(props, onMobileClose, openMobile){
    const {id} = props;
  const classes = useStyles();
  const location = useLocation();

  useEffect(() => {
    if (openMobile && onMobileClose) {
      onMobileClose();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.pathname]);

  const items = [
    {
      href: `/department/287/${id}/home`,
      icon: BarChartIcon,
      title: 'Home'
    },
    {
      href: `/department/287/${id}/article`,
      icon: UsersIcon,
      title: 'Article'
    },
    {
      href: `/department/287/${id}/blog`,
      icon: ShoppingBagIcon,
      title: 'Blog post'
    },
    {
      href: `/department/287/${id}/poll`,
      icon: UserIcon,
      title: 'Poll'
    },
    {
      href: `/department/287/${id}/survey`,
      icon: SettingsIcon,
      title: 'Survey'
    },
    {
      href: `/department/287/${id}/discussion`,
      icon: LockIcon,
      title: 'Discussion'
    },
    {
      href: `/department/287/${id}/event`,
      icon: UserPlusIcon,
      title: 'Event'
    },
    {
      href: `/department/287/${id}/upload-file`,
      icon: AlertCircleIcon,
      title: 'Upload a file'
    },
    {
      href: `/department/287/${id}/idea`,
      icon: BarChartIcon,
      title: 'Submit an idea'
    },
    {
      href: `/department/287/${id}/album`,
      icon: UsersIcon,
      title: 'Photo album'
    },
    {
      href: `/department/287/${id}/gamification`,
      icon: ShoppingBagIcon,
      title: 'Gamification'
    },
    {
      href: `/department/287/${id}/video`,
      icon: UserIcon,
      title: 'Upload a video'
    },
    {
      href: `/department/287/${id}/wiki`,
      icon: SettingsIcon,
      title: 'Wiki page'
    },
    {
      href: `/department/287/${id}/form`,
      icon: LockIcon,
      title: 'Form'
    }
  ];

  const content = (
    <Box
      height="100%"
      display="flex"
      flexDirection="column"
    >
      <Box
        alignItems="center"
        display="flex"
        flexDirection="column"
        p={2}
      >
        {/* <Avatar
          className={classes.avatar}
          component={RouterLink}
          src={user.avatar}
          to="/app/account"
        /> */}
        <Typography
          className={classes.name}
          color="textPrimary"
          variant="h5"
        >
          {id}
        </Typography>
        {/* <Typography
          color="textSecondary"
          variant="body2"
        >
          {user.jobTitle}
        </Typography> */}
      </Box>
      <Divider />
      <Box p={2}>
        <List>
          {items.map((item) => (
            <NavItem
              href={item.href}
              key={item.title}
              title={item.title}
              icon={item.icon}
            />
          ))}
        </List>
      </Box>
    </Box>
  );

  return (
    
      <div className = {classes.Container}>
        <Hidden lgUp>
          <Drawer
            anchor="left"
            classes={{ paper: classes.mobileDrawer }}
            onClose={onMobileClose}
            open={openMobile}
            variant="temporary"
          >
            {content}
          </Drawer>
        </Hidden>
        <Hidden mdDown>
          <Drawer
            anchor="left"
            classes={{ paper: classes.desktopDrawer }}
            open
            variant="persistent"
          >
            {content}
          </Drawer>
        </Hidden>
      </div>
  );
};

ListBar.propTypes = {
  onMobileClose: PropTypes.func,
  openMobile: PropTypes.bool
};

ListBar.defaultProps = {
  onMobileClose: () => {},
  openMobile: false
};

export default ListBar;
