import React, { useState } from 'react'
import classes from './Navbar.module.css'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
import Modal from 'react-bootstrap/Modal'
import Toast from 'react-bootstrap/Toast'
import Spinner from 'react-bootstrap/Spinner'
import Container from 'react-bootstrap/Container'
import axios from 'axios'

import {serverURI} from '../config/default.json'


const Navbar = (props) => {
    
   const { refreshGallery,id} = props;
    const [isLoading, setIsLoading] = useState(false)
    const [showModal, setShowModal] = useState(false)
    const [fileName, setFileName] = useState("Choose Video File");
    const [videoFileData, setVideoFileData] = useState(null);
    const [showToast, setShowToast] = useState(false)
    const [isError, setIsError] = useState(false)

    const handleFile = (e) => {
        setFileName(e.target.files[0].name);
        setVideoFileData(e.target.files[0]);
        console.log(id)
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        console.log(videoFileData)

        var submitData = new FormData();

        submitData.append("videoFile", videoFileData);
        submitData.append("department", id)
        const config = { headers: { 'Content-Type': 'multipart/form-data' } };
        setIsLoading(true);
        axios.post( serverURI + '/api/video', submitData, config)
            .then(function (response) {
                console.log(response);
                setIsLoading(false);
                setShowModal(false);
                setShowToast(true);
                setIsError(false);
                refreshGallery();
                setFileName("Choose Video File")
            })
            .catch(function (error) {
                console.log(error);
                setIsError(true);
                setIsLoading(false);
                setShowModal(false);
                setShowToast(true);
            })
    }

    return (
        <div className={`${classes.wrapper} bg-light`}>
            <Container>
                <Button onClick={() => setShowModal(true)} variant="outline-secondary">Add a New Video</Button>

                <Modal show={showModal} onHide={() => setShowModal(false)} style = {{marginTop: "200px"}}>
                    <Form onSubmit={handleSubmit}>
                        <Modal.Header closeButton>
                            <Modal.Title>Select Video</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <Form.File
                                id="custom-file"
                                accept="video/mp4,video/x-m4v,video/*"
                                label={fileName}
                                onChange={handleFile}
                                custom
                                required
                            />
                        </Modal.Body>
                        <Modal.Footer>
                            <Button variant="secondary" onClick={() => setShowModal(false)}>
                                Close
                             </Button>
                            <Button className={classes.submitBtn} type="submit" variant="primary" disabled={isLoading}>
                                {isLoading ? <Spinner
                                    // as="span"
                                    // size="sm"
                                    animation="border"
                                    role="status"
                                    aria-hidden="true"
                                /> : "Upload Video"}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Toast className={classes.toastStyle} onClose={() => setShowToast(false)} show={showToast} delay={30000} autohide>
                    <Toast.Header>
                        <img
                            src="holder.js/20x20?text=%20"
                            className="rounded mr-2"
                            alt=""
                        />
                        <strong className="mr-auto">{isError ? "Ooops!" : "Success!"}</strong>
                        <small>{fileName}</small>
                    </Toast.Header>
                    <Toast.Body>{isError ? "Something went wrong while uploading the video." : "Woohoo, you've successfully uploaded your video!"}</Toast.Body>
                </Toast>
            </Container>
        </div>
    )
}

export default Navbar
