import React, { useState, useRef, useEffect } from 'react';
import {useHistory} from 'react-router-dom';
import {useParams} from "react-router"
import ArticleImage from "./articleImage/article-image"
import { Typography, Box, Grid, TextField,Select, InputLabel, RadioGroup, FormControlLabel, Radio, Checkbox, Button, FormControl } from '@material-ui/core';
import Editor from "./editor/editor"
import Fileuploader from './fileupload/fileupload'
import { format } from 'date-fns';
import moment from 'moment';
import Axios from 'axios';
 

function CreateArticle1(props){

  const history = useHistory();
  const {match} = props;
  let {id} = match.params;

  const [SaveData, setSaveData] = useState(false);
  const Now_date = moment(new Date()).format('YYYY-MM-DD\T\hh:mm');


  const [EditorHtml, setEditorHtml] = useState('');
  const [Imagepath, setImagepath] = useState("");
  const [Status, setStatus] = useState();
  const [Category, setCategory] = useState("news");
  const Headline = useRef();
  const Summary = useRef();
  const Author = useRef();
  const [PublishDate, setPublishDate] = useState(moment(new Date()).format('YYYY-MM-DD\T\hh:mm'));
  const [ExpireDate, setExpireDate] = useState(moment(new Date()).format('YYYY-MM-DD\T\hh:mm'));
  const [Checked1, setChecked1] = useState(true);
  const [Checked2, setChecked2] = useState(true);
  const [Checked3, setChecked3] = useState(true);

  const Save_article =(e)=>{
    e.preventDefault();
    setSaveData(true);
    console.log(EditorHtml)
    Axios({
      method: "POST",
      url: "https://start.azintranet.com:5000/api/g-article/create-article",
      data: {
        department: id,
        headline: Headline.current.value,
        summary: Summary.current.value,
        body: EditorHtml.toString(),
        author: Author.current.value,
        publish: PublishDate,
        expire: ExpireDate,
        featured: Checked1,
        anonymous: Checked2,
        reading: Checked3,
        image: Imagepath,
        comment: 0
        // attached_file: attached_file




        
      }
    }).then(res=>{
      history.push(`/group/287/${id}/article`)
    })
  }

  const Update_body = (count) =>{
      console.log(count);
      setEditorHtml(String(count))
      console.log(EditorHtml)
  }

  const Update_image = (count) =>{
    setImagepath(String(count))
    console.log(Imagepath)
  }

  
  useEffect(()=>{
    if(Imagepath){
      console.log(Imagepath)
      console.log(EditorHtml)
    }
  })


  const PublishDate_onChange = (e,x) =>{
    setPublishDate(e.target.value)
    console.log(Checked2)
  }

  const ExpireDate_onChange = (e,x) =>{
    setExpireDate(e.target.value)
    console.log(ExpireDate)
  }

  return (
    <div className = "CreateDepartment1Container">
      <form onSubmit = {Save_article}>
        <Box mb = {5}><Typography variant = "h4">Add Article</Typography></Box>
        <Grid container spacing={6}>
              <Grid item sm = {12} xs={12} md={5}>
                      <Box><Typography variant = "h6">Headline</Typography></Box>
                      <TextField
                          variant="outlined"
                          margin="normal"
                          fullWidth
                          required
                          label="Headline"
                          inputRef = {Headline}
                          error={Status === "Please input your name of department"}
                          autoComplete="off"
                          type="text"
                          onChange={() => {
                              if (Status === "Please input your name of department") {
                                  setStatus(null);
                              }
                          }}
                          helperText={(() => {
                              if(Status === "Please input your name of department")
                              {
                                  return "email already exist"
                              }
                              return null;
                          })()}
                          FormHelperTextProps={{ error: true }}
                      />
                      <Box><Typography variant = "h6">Summary</Typography></Box>
                      <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        required
                        label="Summary"
                        inputRef = {Summary}
                        multiline
                        rows = {3}
                        error={Status === "Please input your name of department"}
                        autoComplete="off"
                        type="text"
                        onChange={() => {
                            if (Status === "Please input your name of department") {
                                setStatus(null);
                            }
                        }}
                        helperText={(() => {
                            if(Status === "Please input your name of department")
                            {
                                return "email already exist"
                            }
                            return null;
                        })()}
                        FormHelperTextProps={{ error: true }}
                    />
                    <Box mt = {1}><Typography variant = "h6">Article body</Typography></Box>
                    <Editor Update_body = {Update_body} />
              </Grid>
              <Grid item sm = {6} xs={12} md={4}>
                <Box mb = {1}><Typography variant = "h6">Upload a featured image</Typography></Box>
                <ArticleImage
                  Update_image = {Update_image}
                  uploadURL = "https://start.azintranet.com:5000/api/department/article/upload-image"
                  name = {id}
                  size = {100}/>
                <Box mt = {1}><Typography variant = "h6">Attach files</Typography></Box>
                <Fileuploader />
                
              </Grid>
              <Grid item sm = {6} xs={12} md={3}>
                    <Box><Typography variant = "h6">Article setting</Typography></Box>
                    <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        label="Author"
                        inputRef = {Author}
                        error={Status === "Please input your name of department"}
                        autoComplete="off"
                        type="text"
                        onChange={() => {
                            if (Status === "Please input your name of department") {
                                setStatus(null);
                            }
                        }}
                        helperText={(() => {
                            if(Status === "Please input your name of department")
                            {
                                return "email already exist"
                            }
                            return null;
                        })()}
                        FormHelperTextProps={{ error: true }}
                    />
                    <Box mb = {1} mt = {3}><Typography variant = "h6">publication date</Typography></Box>
                    <TextField
                      id="datetime-local"
                      type="datetime-local"
                      value = {PublishDate}
                      onChange = {PublishDate_onChange}
                      defaultValue={Now_date}
                      InputLabelProps={{
                        shrink: true,
                        }}
                    />
                    <Box mb = {1} mt = {2}><Typography variant = "h6">expiration date</Typography></Box>
                    <TextField
                      id="datetime-local"
                      type="datetime-local"
                      value = {ExpireDate}
                      onChange = {ExpireDate_onChange}
                      defaultValue={Now_date}
                      InputLabelProps={{
                        shrink: true,
                        }}
                    />
                    <Box mt = {3} mb = {1}><Typography variant = "h6">features</Typography></Box>
                    <Box>
                        <FormControlLabel
                            control={<Checkbox checked={Checked1} onChange={()=>setChecked1(!Checked1)} name="checkedG" />}
                            label="Featured"
                        />
                    </Box>
                    <Box>
                        <FormControlLabel
                            control={<Checkbox checked={Checked2} onChange={()=>setChecked2(!Checked2)} name="checkedG" />}
                            label="Allow anonymous comments"
                        />
                    </Box>
                    <Box mb = {3}>
                        <FormControlLabel
                            control={<Checkbox checked={Checked3} onChange={()=>setChecked3(!Checked3)} name="checkedG" />}
                            label="Required reading"
                        />
                    </Box>
                    <Button variant = "contained" color = "primary" type = "submit" >Publish and Save</Button>
              </Grid>
        </Grid>
      </form>
    </div>
  )
} 

export default CreateArticle1;