import React, { useState } from 'react'
import classes from './VGallery.module.css'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Modal from 'react-bootstrap/Modal'

import VideoBox from '../videoBox/VideoBox'

import { serverURI } from '../config/default.json'

const VGallery = ({ data }) => {

    const [showVideo, setShowVideo] = useState(false);
    const [videoIdem, setVideoIdem] = useState(data[0]);

    const showModal = (item) => {
        console.log(item)
        setVideoIdem(item);
        setShowVideo(true);
    }

    return (
        <div>
            <Row>
                {
                    data && data.map((item, index) => (
                        <Col key={index} xs={12} sm={6} md={4} lg={3}>
                            <div className={`${classes.videoCard} shadow`} onClick={() => showModal(item)}>
                                <img src={serverURI + '/api/video/thumbnails/' + item.fileName} alt="thumbnail" />
                                <p>{item.title}</p>
                            </div>
                        </Col>
                    ))
                }
            </Row>
            {
                showVideo && <VideoBox videoData={videoIdem} closeVideo={() => setShowVideo(false)} />
            }

        </div>
    )
}

export default VGallery
