import React from 'react'
import classes from './VideoBox.module.css'
import { serverURI } from '../config/default.json'

const VideoBox = ({ videoData, closeVideo }) => {
    return (
        <div className={classes.wrapper}>
            <div className={classes.closeArea} onClick={closeVideo}></div>
            <video src={serverURI + '/api/video/videos/' + videoData.fileName} playsInline controls autoPlay />
        </div>
    )
}

export default VideoBox
