

import React, { useState, useCallback, useEffect } from "react";
import {useHistory} from "react-router-dom"
import {
    withStyles,
    Grid,
    Button,
    Box
  } from "@material-ui/core";
import { makeStyles } from '@material-ui/core/styles';
import Gallery from "react-photo-gallery";
import Carousel, { Modal, ModalGateway } from "react-images";
import Sidebar from '../../../Sidebar/Sidebar'
import UploadPhoto from "./uploadPhoto";
import AddIcon from '@material-ui/icons/AddCircleOutline';
import MultiImageInput from 'react-multiple-image-input';
import Axios from 'axios';

// import {photos} from "./photo"

const useStyles = makeStyles((theme) => ({
    mainGrid: {
      marginTop: theme.spacing(3),
    },
    Article_container_desktop:{
      width: "calc(100% - 256px)",
      display: "inline-block",
      margin: "auto",
      marginTop: "30px"
    },
    Article_container_mobile:{
      width: "100%",
      display: "inline-block",
      margin: "auto",
      marginTop: "30px"
    }
    
  }));

function AlbumPhotos(props) {

    const {match} = props;
    let {id} = match.params;
    let {type} = match.params;
    const classes = useStyles();
    const history  = useHistory();
    
  const [Get_flag, setGet_flag] = useState(true);
  const [currentImage, setCurrentImage] = useState(0);
  const [viewerIsOpen, setViewerIsOpen] = useState(false);
  const [WidthSet, setWidthSet] = useState(window.innerWidth);
  var photoCount= 0
  const [photos, setPhotos] = useState([]); 
  const [images, setImages] = useState({});
  const UploadedImage = React.createRef();
useEffect(()=>{
  if(Get_flag){
  Axios({
    method: "POST",
    url: "https://start.azintranet.com:5000/api/album/get-photo-count",
    data:{
        department: id,
        album_id: type
    }
}).then((res)=>{
    var photos = [];
    photoCount = res.data
    console.log(photoCount)
    for(let i = 0; i<photoCount; i++){
      photos.push(
      {
          src: `https://start.azintranet.com:5000/uploads/department/${id}/album/${type}/photos/${i}.jpg`,
          width: 4,
          height: 3
      }
      )
    }
    setPhotos(photos)
    setGet_flag(false)
  })
}
})



  const openLightbox = useCallback((event, { photo, index }) => {
    setCurrentImage(index);
    setViewerIsOpen(true);
  }, []);

  const closeLightbox = () => {
    setCurrentImage(0);
    setViewerIsOpen(false);
  };

  useEffect(()=>{
    window.addEventListener('resize', function() {
      setWidthSet(window.innerWidth);
    });
  })


  return (
    <div>
            <Sidebar id = {id}/>
        <Grid container spacing={4} className = {WidthSet >= 1280 ? classes.Article_container_desktop : classes.Article_container_mobile}>
           <UploadPhoto department = {id} album_id = {type}/>
            {/* <div class="row article-body"><img src = {images[0]}/></div> */}
            <Gallery photos={photos} onClick={openLightbox} />
            <ModalGateway>
                {viewerIsOpen ? (
                <Modal onClose={closeLightbox}>
                    <Carousel
                    currentIndex={currentImage}
                    views={photos.map(x => ({
                       ...x,
                        srcset: x.srcSet,
                        caption: x.title
                    }))}
                    />
                </Modal>
                ) : null}
            </ModalGateway>
        </Grid>
    </div>
  );
}
export default AlbumPhotos;
