import React, { useState, useEffect } from 'react'
import Navbar from './navbar/Navbar'
import VGallery from './vGallery/VGallery'
import Sidebar from "../Sidebar/Sidebar"
import Container from 'react-bootstrap/Container'

import { serverURI } from './config/default.json'
import "./videoStyle.css"

import axios from 'axios'

function Video(props) {

  const { match } = props;
  let {id} = match.params; 


  const [data, setData] = useState('');
  const [WidthSet, setWidthSet] = useState(window.innerWidth);

  useEffect(() => {
    refreshGallery()
  }, [])

  useEffect(()=>{
    window.addEventListener('resize', function() {
      setWidthSet(window.innerWidth);
    });
  })

  const refreshGallery = () => {
    // axios.get('https://start.azintranet.com:5000/api/video')
    axios({
      method: "GET",
      url: "https://start.azintranet.com:5000/api/g-video",
      params:{
        department: id
      }
    })
      .then((res) => {
        console.log(res.data)
        let newVideos = [...data];
        newVideos = res.data;
        setData(newVideos);
      })
  }

  return (
    <div>
      <Sidebar id = {id}/>
      <div className = {WidthSet >= 1280 ? "Article_container_desktop" : "Article_container_mobile"}>
        <Navbar refreshGallery={refreshGallery} id = {id} />
        <Container>
          {/* <VGallery data={data} /> */}
          {!data.length?
            <div style = {{textAlign: "center", marginTop: "25%"}}>
              <h1>There is nothing in this video</h1>
            </div>
             :<VGallery data={data} />
          }
        </Container>
      </div>
    </div>
  );
}

export default Video;
