import React, {useEffect, useState, useCallback} from 'react';
import {useHistory} from "react-router-dom"
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import {Button} from '@material-ui/core'
import Container from '@material-ui/core/Container';
import FeaturedPost from './articlePost';
import AddIcon from '@material-ui/icons/AddCircleOutline';
import Axios from 'axios';
import Sidebar from '../Sidebar/Sidebar'

const useStyles = makeStyles((theme) => ({
  mainGrid: {
    marginTop: theme.spacing(3),
  },
  Article_container_desktop:{
    width: "calc(100% - 256px)",
    display: "inline-block",
    margin: "auto",
    marginTop: "30px"
  },
  Article_container_mobile:{
    width: "100%",
    display: "inline-block",
    margin: "auto",
    marginTop: "30px"
  }
  
}));

function Blog(props) {

  const {match} = props;
  let {id} = match.params;

  const classes = useStyles();
  const [emptyArticle, setEmptyArticle] = useState(false);
  const [flag, setFlag] = useState(true);
  const [flag_check, setFlag_check] = useState(id);
  const [iden, setIden] = useState('');
  const [department, setDepartment] = useState('');
  const [Image_url, setImage_url] = useState('');
  const [Headline, setHeadline] = useState('');
  const [Summary, setSummary] = useState('');
  const [WidthSet, setWidthSet] = useState(window.innerWidth);
  const [ArticleList, setArticleList] = useState([]);
  const history = useHistory();



  var Articlebox = [];

  useEffect(()=>{
    if(flag_check != id)
    setFlag(true)
    if(flag){
      setFlag_check(id);
      Axios({
        method: "POST",
        url: "https://start.azintranet.com:5000/api/p-blog/get-blogs",
      }).then(res=>{ 
        
        res.data.map((article)=>{
       
          // setImage_url('https://start.azintranet.com:5000/'+String(article.image))
          // setHeadline(String(article.headline))
          // setSummary(String(article.summary))
          // setIden(String(article._id))
          // setDepartment(String(article.department))
          // featuredPosts[0].image = image_url
          // console.log(featuredPosts[0].image)

          if(article.department === id){
            console.log("good")
            Articlebox.push({
              department: article.department,
              iden: article._id,
              title: article.headline,
              date: article.headline,
              description: article.summary,
              image: 'https://start.azintranet.com:5000/'+article.image,
              imageText: 'Image Text',
            })
            
          }
          
          
        })
        setFlag(false)
        setArticleList(Articlebox)
        console.log(ArticleList)
        if(Articlebox.length>0){
          setEmptyArticle(false)
          console.log("started")
        }
        else{
          setEmptyArticle(true)
        }
        
      })
      .catch(e=>{
        console.log(e)
        setFlag(false)
        setEmptyArticle(true)
      })
    }
  
    
  })

  useEffect(()=>{
    window.addEventListener('resize', function() {
      setWidthSet(window.innerWidth);
    });
  })
 


  return (
    <div key = {id}>
      <Sidebar id = {id}/>
        <CssBaseline />
        <Grid container spacing={4} className = {WidthSet >= 1280 ? classes.Article_container_desktop : classes.Article_container_mobile}>
          <div style = {{textAlign: "center"}}>
            <Button variant = "contained" color = "primary" startIcon = {<AddIcon/>} onClick = {()=>history.push(`/project/287/${id}/create-blog`)}>Add Blog</Button>
          </div>

          {!emptyArticle?ArticleList.map((post) => (
            <FeaturedPost post={post} iden = {post.iden} department = {post.department} />
          ))
          :<div style = {{textAlign: "center", marginTop: "20%"}}>
              <h1>There is nothing in this Blog</h1>
          </div>}
        </Grid>
    </div>
  );
}

Blog.propTypes = {
    match: PropTypes.bool
  };

  export default Blog;