import React, {Component, useState, useRef} from 'react';
import Breadcrumb from "./breadcrumb";
import Sidebar from '../../Sidebar/Sidebar'
import Axios from 'axios';
import {TextField} from "@material-ui/core";
import swal from "sweetalert";
import { connect, useDispatch } from "react-redux";

function ArticleDetails(props) {

    const {match,auth} = props;
    let {id} = match.params;
    let {type} = match.params;
    const [articleImage, setArticleImage] = useState();
    const [articleHeadline, setArticleHeadline] = useState();
    const [articleAuthor, setArticleAuthor] = useState();
    const [articleBody, setArticleBody] = useState();
    const [date, setDate] = useState();

    const [Status, setStatus] = useState(null);

    const CommentMessage = useRef();

    const [gettedcomment, setGettedcomment] = useState([]);
    const [flag, setFlag] = useState(true)

    const leaveComment = (e) =>{
        e.preventDefault();
        Axios({
            method: "POST",
            url: `https://start.azintranet.com:5000/api/comments/leave-comment`,
            data: {
                name: auth.user.name,
                message: CommentMessage.current.value,
                comment: type
            }
        }).then(res=>{
            console.log("dddddddddddddddddddddddddd")
            swal("succecss!", "Thanks for your comment", "success")
        }).catch(err=>{
            if(err.response.status === 400){
                swal("Oh OOps sorry!", "you already left comment, you can't leave comment again", "error")
            }
        })
    }

    if(flag){
    Axios({
        method: "POST",
        url: `https://start.azintranet.com:5000/api/blog/get-fullarticle/${type}`
    }).then(res=>{
        console.log("great")
        setArticleImage(res.data.image)
        setArticleHeadline(res.data.headline)
        setArticleAuthor(res.data.author)
        setArticleBody(res.data.body)
        setDate(res.data.date);
    })

    Axios({
        method: "POST",
        url: `https://start.azintranet.com:5000/api/comments/get-comments`,
        data: {
            comment: type
        }
    }).then(res=>{
        var tempComment = [];
        res.data.map((value,index)=>{
            tempComment.push(value);
        })
        console.log("gooogd", tempComment)
        setGettedcomment(tempComment);
        setFlag(false)
    })
}

        return (
            <div className  = "row">
            <div>
                <Sidebar id = {id}/>
            </div>
            <div style = {{margin: "auto", paddingLeft: "10px", paddingRight: "10px"}}>
                <Breadcrumb title={'Article - Details'}/>
                {/*Blog Details section*/}
                <section className="blog-detail-page section-b-space">
                    <div className="container">
                        <div className="row">
                            <div className="col-sm-12 blog-detail">
                                <img src={`https://start.azintranet.com:5000/${articleImage}`} className="img-fluid" alt=""/>
                                <h3>{articleHeadline}</h3>
                                <ul className="post-social">
                                    <li>{date}</li>
                                    <li>Posted By: {articleAuthor}</li>
                                    <li><i className="fa fa-comments"></i>{` ${gettedcomment.length}`} Comment</li>
                                </ul>
                                <div class="row article-body" style = {{display: "inline-block"}} dangerouslySetInnerHTML={{__html: articleBody}}></div>
                            </div>
                        </div>
                        <div className="row section-b-space">
                            <div className="col-sm-12">
                                <ul className="comment-section">
                                {gettedcomment.map((value, index)=>{
                                    return(
                                    <li>
                                        <div className="media">
                                            {/* <img src={`${process.env.PUBLIC_URL}/assets/images/avtar.jpg`} alt="Generic placeholder image" /> */}
                                                <div className="media-body">
                                                    <h6>{value.name} <span>{value.date}</span></h6>
                                                    <p style = {{whiteSpace: "pre-line"}}>{value.message}</p>
                                                </div>
                                        </div>
                                    </li>
                                    )
                                })
                                }
                                </ul>
                            </div>
                        </div>
                        <div className="row blog-contact">
                            <div className="col-sm-12">
                                <h2>Leave Your Comment</h2>
                                <form className="theme-form" onSubmit = {leaveComment}>
                                    <div className="form-row">
                                        <div className="col-md-12">
                                            <label htmlFor="exampleFormControlTextarea1">Comment</label>
                                            <TextField
                                                variant="outlined"
                                                margin="normal"
                                                fullWidth
                                                required
                                                multiline
                                                rows = {5}
                                                label="Please input your Comment"
                                                error={Status === "Please input your name of department"}
                                                inputRef= {CommentMessage}
                                                autoComplete="off"
                                                type="text"
                                                onChange={() => {
                                                    if (Status === "Please input your name of department") {
                                                        setStatus(null);
                                                    }
                                                }}
                                                helperText={(() => {
                                                    if(Status === "Please input your name of department")
                                                    {
                                                        return "email already exist"
                                                    }
                                                    return null;
                                                })()}
                                                FormHelperTextProps={{ error: true }}
                                            />
                                        </div>
                                        <div className="col-md-12">
                                            <button className="btn btn-solid" type="submit">Post Comment</button>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </section>

            </div>
            </div>
        )
    }

    const mapStateToProps = state => ({
        auth: state.auth
      });
      
      export default connect(
        mapStateToProps
      )(ArticleDetails);