import React from 'react';
import {Link} from 'react-router-dom'
import Sidebar from "../department/Sidebar/Sidebar"

function Temp(props){
    const {match} = props;
    let {id} = match.params;
    return(
        <div class="container404">
            <Sidebar id = {id}/>
            <div class="mainbox404">
                <h1 style = {{textAlign: "center", paddingTop: "30vh"}}>We are still developing this functionality,<br/> please check back later</h1>
                
            </div>
        </div>
    )
}

export default Temp;