import React, {useState} from 'react';
import { DropzoneArea } from 'material-ui-dropzone';;

function Fileupload(){
    const [formact, setformact] = useState(true);
    const FileUploader= async (files)=>{
        // const files = e.target.value;
        if(files){
            console.log(files)
            var xhr = new XMLHttpRequest();
            const requestData = new FormData();
            files.map((file) => {
                requestData.append("attachedFile", file);
            } );

  
            
            
            xhr.addEventListener("readystatechange", function() {
                if(this.readyState === 4) {
                if(this.response.status === "error"){
                   console.log("not good");
                } else {
                    console.log(this.response) 
                }
                } 
            });
            xhr.upload.addEventListener("progress", progressFunction, false);  
            xhr.open("POST", "https://start.azintranet.com:5000/api/project/article/upload-files");
            xhr.setRequestHeader('X-Requested-With','XMLHttpRequest');
            xhr.send(requestData);

            function progressFunction(evt){  
                if (evt.lengthComputable) {  
                    console.log(Math.round(evt.loaded / evt.total * 100))
                  if(Math.round(evt.loaded / evt.total * 100) === 100){
                    setformact(true);
                  }
                }  
            } 
            }

}
    return(
        <div>
            <DropzoneArea
                showFileNames = "true"
                onChange={FileUploader}
                // onChange={(files) => console.log('Files:', files)}
            />
        </div>
    )
}

export default Fileupload;