import React, { Component } from 'react';
import Axios from 'axios'

export default class MultipleImageUploadComponent extends Component {

    fileObj = [];
    fileArray = [];

    constructor(props) {
        super(props)
        this.state = {
            file: [null],
            formfact : ''
        }
        this.uploadMultipleFiles = this.uploadMultipleFiles.bind(this)
        this.uploadFiles = this.uploadFiles.bind(this)
    }

    uploadMultipleFiles(e) {
     
        
      //  this.fileObj.push(e.target.files)
        this.fileObj = e.target.files;
       

        // console.log(this.fileObj.files instanceof Array);
        this.fileArray = [];
        for (let i = 0; i < this.fileObj.length; i++) {
            this.fileArray.push(URL.createObjectURL(this.fileObj[i]))
        }
        // this.fileObj.files.map(url => (URL.createObjectURL(url)));

        this.setState({ file: this.fileObj });
    }

    uploadFiles(e) {
        e.preventDefault()
        if(this.state.file[0]!=null){
            console.log(this.state.file[0])
            console.log("WOW"+this.state.file.length)
        var xhr = new XMLHttpRequest();
        const UploadPhotos = new FormData();
        console.log(this.state.file)
        UploadPhotos.append("department", this.props.department)
        UploadPhotos.append("album_id", this.props.album_id)
        xhr.addEventListener("readystatechange", function() {
            if(this.readyState === 4) {
              if(this.response.status === "error"){
              } else {
                console.log(this.response)
                if(this.response=='"success"') 
                window.location.reload();
                console.log("wow")
              }
            } 
        });
        xhr.upload.addEventListener("progress", progressFunction, false);  
        xhr.open("POST", "https://start.azintranet.com:5000/api/album/create-album-photos");

        function progressFunction(evt){  
            if (evt.lengthComputable) {  
                console.log(Math.round(evt.loaded / evt.total * 100))
              if(Math.round(evt.loaded / evt.total * 100) === 100){
                this.formfact = "100"
              }
            }  
        } 
        for(let i = 0; i<this.state.file.length; i++){
            UploadPhotos.append(`${i}`, this.state.file[i])
            }
        xhr.send(UploadPhotos);
    }
    }
    

    render() {
        return (
            <form>
                <div className="form-group multi-preview">
                    {
                        
                        (this.fileArray || []).map((url,index) => (
                   //     console.log(index,"===========",url),
                        <img src={url} alt="..." key={index} style = {{width: '200px'}}/>
                    ))}
                </div>

                <div className="form-group">
                    <input type="file" className="form-control" onInput={this.uploadMultipleFiles} multiple />
                </div>
                <button type="button" className="btn btn-danger btn-block" onClick={this.uploadFiles}>Upload</button>
            </form>
        )
    }
}