import React, {Fragment, useState, useRef} from 'react';
import {useHistory} from "react-router-dom";
import { Typography, Box, Grid, TextField, RadioGroup, FormControlLabel, Radio, Checkbox, Button } from '@material-ui/core';
import SaveIcon from '@material-ui/icons/Save';
import Axios from 'axios';

function CreateProject1(){

    //create status state

    const [Status, setStatus] = useState(null);

    //set Usehistory

    const history= useHistory();

    //inputtext form

    const DepartmentName = useRef();
    const DepartmentDescription = useRef();

    //Checked state
    const [CheckedArticle, setCheckedArticle] = useState(true);
    const [CheckedBlog, setCheckedBlog] = useState(true);
    const [CheckedPoll, setCheckedPoll] = useState(true);
    const [CheckedSurvey, setCheckedSurvey] = useState(true);
    const [CheckedDiscussion, setCheckedDiscussion] = useState(true);
    const [CheckedEvent, setCheckedEvent] = useState(true);
    const [CheckedUploadFile, setCheckedUploadFile] = useState(true);
    const [CheckedIdea, setCheckedIdea] = useState(true);
    const [CheckedAlbum, setCheckedAlbum] = useState(true);
    const [CheckedGamification, setCheckedGamification] = useState(true);
    const [CheckedUploadVideo, setCheckedUploadVideo] = useState(true);
    const [CheckedWiki, setCheckedWiki] = useState(true);
    const [CheckedForm, setCheckedForm] = useState(true);

    //Radio state
    const [DepartmentType, setDepartmentType] = useState("public");
    const [DepartmentModerate, setDepartmentModerate] = useState("withAdmin");
    const SaveDepartment=(e)=>{
        e.preventDefault();
        
        Axios({
            method: "POST",
            url: "https://start.azintranet.com:5000/api/project/create-department",
            data: {
                name: DepartmentName.current.value,
                description: DepartmentDescription.current.value,
                type: DepartmentType,
                moderate: DepartmentModerate,
                article: CheckedArticle,
                blog: CheckedBlog,
                poll: CheckedPoll,
                survey: CheckedSurvey,
                discussion: CheckedDiscussion,
                event: CheckedEvent,
                uploadFile: CheckedUploadFile,
                idea: CheckedIdea,
                album: CheckedAlbum,
                gamification: CheckedGamification,
                uploadVideo: CheckedUploadVideo,
                wiki: CheckedWiki,
                form: CheckedForm
            }
        }).then(response=>{
            if(response.data){
                history.push("/project/287/"+DepartmentName.current.value+"/Home");
                window.location.reload();
            }
        }).catch(err=>{
            console.log("very bad action")
            setStatus("Please input your name of department")
        })
    }
    return(
        <div className = "CreateDepartment1Container">
            <form onSubmit = {SaveDepartment}>
            <Grid container spacing={6}>
                <Grid item sm = {12} xs={12} md={6}>
                    <Box mb = {4}><Typography variant = "h4">Create a Project</Typography></Box>
                    <Box><Typography variant = "h6">Project Name</Typography></Box>

                    <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        required
                        label="Project Name"
                        error={Status === "Please input your name of department"}
                        inputRef= {DepartmentName}
                        autoComplete="off"
                        type="text"
                        onChange={() => {
                            if (Status === "Please input your name of department") {
                                setStatus(null);
                            }
                        }}
                        helperText={(() => {
                            if(Status === "Please input your name of department")
                            {
                                return "email already exist"
                            }
                            return null;
                        })()}
                        FormHelperTextProps={{ error: true }}
                    />

                    <Box mt = {4}><Typography variant = "h6">Project Description</Typography></Box>

                    <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        required
                        label="Project Description"
                        inputRef= {DepartmentDescription}
                        multiline
                        rows = {5}
                        autoComplete="off"
                        type="text"
                        FormHelperTextProps={{ error: true }}
                    />

                    <Box mt = {4}><Typography variant = "h6">Type of Project</Typography></Box>

                    <RadioGroup style = {{textAlign:"center"}} aria-label="gender" name="DepartmentType" value= {DepartmentType} onChange= {(e)=>{setDepartmentType(e.target.value)}} >
                        <FormControlLabel value="public" control={<Radio />} label="Public Project" />
                        <FormControlLabel value="private" control={<Radio />} label="Private Project" />
                    </RadioGroup>

                    <Box mt = {4}><Typography variant = "h6">Moderate user registrations</Typography></Box>

                    <RadioGroup style = {{textAlign:"center"}} aria-label="gender" name="ModerateRegistration" value= {DepartmentModerate} onChange= {(e)=>{setDepartmentModerate(e.target.value)}}>
                        <FormControlLabel value="withAdmin" control={<Radio />} label="they must be approved by an Admin" />
                        <FormControlLabel value="withoutAdmin" control={<Radio />} label="Peopel can get access with his self" />
                    </RadioGroup>
                </Grid>
                <Grid item sm = {12} xs={12} md={6}>
                    <Box mb = {4}><Typography variant = "h4">Project application settings</Typography></Box>
                    <Box>
                        <FormControlLabel
                            control={<Checkbox checked={CheckedArticle} onChange={()=>setCheckedArticle(!CheckedArticle)} name="checkedG" />}
                            label="Article"
                        />
                    </Box>
                    <Box>
                        <FormControlLabel
                            control={<Checkbox checked={CheckedBlog} onChange={()=>setCheckedBlog(!CheckedBlog)} name="checkedG" />}
                            label="Blog Post"
                        />
                    </Box>
                    <Box>
                        <FormControlLabel
                            control={<Checkbox checked={CheckedPoll} onChange={()=>setCheckedPoll(!CheckedPoll)} name="checkedG" />}
                            label="Poll"
                        />
                    </Box>
                    <Box>
                        <FormControlLabel
                            control={<Checkbox checked={CheckedSurvey} onChange={()=>setCheckedSurvey(!CheckedSurvey)} name="checkedG" />}
                            label="Survey"
                        />
                    </Box>
                    <Box>
                        <FormControlLabel
                            control={<Checkbox checked={CheckedDiscussion} onChange={()=>setCheckedDiscussion(!CheckedDiscussion)} name="checkedG" />}
                            label="Discussion"
                        />
                    </Box>
                    <Box>
                        <FormControlLabel
                            control={<Checkbox checked={CheckedEvent} onChange={()=>setCheckedEvent(!CheckedEvent)} name="checkedG" />}
                            label="Event"
                        />
                    </Box>
                    <Box>
                        <FormControlLabel
                            control={<Checkbox checked={CheckedUploadFile} onChange={()=>setCheckedUploadFile(!CheckedUploadFile)} name="checkedG" />}
                            label="Upload a file"
                        />
                    </Box>
                    <Box>
                        <FormControlLabel
                            control={<Checkbox checked={CheckedIdea} onChange={()=>setCheckedIdea(!CheckedIdea)} name="checkedG" />}
                            label="Submit an idea"
                        />
                    </Box>
                    <Box>
                        <FormControlLabel
                            control={<Checkbox checked={CheckedAlbum} onChange={()=>setCheckedAlbum(!CheckedAlbum)} name="checkedG" />}
                            label="Photo album"
                        />
                    </Box>
                    <Box>
                        <FormControlLabel
                            control={<Checkbox checked={CheckedGamification} onChange={()=>setCheckedGamification(!CheckedGamification)} name="checkedG" />}
                            label="Gamification"
                        />
                    </Box>
                    <Box>
                        <FormControlLabel
                            control={<Checkbox checked={CheckedUploadVideo} onChange={()=>setCheckedUploadVideo(!CheckedUploadVideo)} name="checkedG" />}
                            label="Upload a video"
                        />
                    </Box>
                    <Box>
                        <FormControlLabel
                            control={<Checkbox checked={CheckedWiki} onChange={()=>setCheckedWiki(!CheckedWiki)} name="checkedG" />}
                            label="Wiki page"
                        />
                    </Box>
                    <Box mb = {4}>
                        <FormControlLabel
                            control={<Checkbox checked={CheckedForm} onChange={()=>setCheckedForm(!CheckedForm)} name="checkedG" />}
                            label="Form"
                        />
                    </Box>
                    <Button
                        type = "submit"
                        variant="contained"
                        color="primary"
                        size="large"
                        startIcon={<SaveIcon />}
                    >
                        Save Project
                    </Button>
                </Grid>
            </Grid>
            </form>
        </div>
    )
}

export default CreateProject1;