import React, { useState, useCallback, useEffect } from "react";
import PropTypes from "prop-types";
import { makeStyles } from '@material-ui/core/styles';
import {useHistory} from "react-router-dom"
import {
  Grid,
  TablePagination,
  Divider,
  Toolbar,
  Typography,
  Button,
  Paper,
  Box,
  withStyles,
} from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import LaunchRoundedIcon from '@material-ui/icons/LaunchRounded';
import SelfAligningImage from "../../../../../shared/components/SelfAligningImage";
import HighlightedInformation from "../../../../../shared/components/HighlightedInformation";
import ConfirmationDialog from "../../../../../shared/components/ConfirmationDialog";
import ConsecutiveSnackbarMessages from "../../../../../shared/components/ConsecutiveSnackbarMessages";

import Sidebar from "../Sidebar/Sidebar"
import Axios from "axios";

const useStyles = makeStyles((theme) => ({
    mainGrid: {
      marginTop: theme.spacing(3),
    },
    Article_container_desktop:{
      width: "calc(100% - 256px)",
      display: "inline-block",
      margin: "auto",
      marginTop: "30px",
      float: "right"
    },
    Article_container_mobile:{
      width: "100%",
      display: "inline-block",
      margin: "auto",
      marginTop: "30px"
    }
    
  }));

const styles = {
  dBlock: { display: "block" },
  dNone: { display: "none" },
  toolbar: {
    justifyContent: "center",
  },
};

const rowsPerPage = 25;

function shuffle(array) {
    for (let i = array.length - 1; i > 0; i--) {
      let j = Math.floor(Math.random() * (i + 1));
      [array[i], array[j]] = [array[j], array[i]];
    }
  }

function Album(props) {
  const {
    openAddPostModal
  } = props;
  const {match} = props;
  let {id} = match.params;
  const classes = useStyles();
  const [page, setPage] = useState(0);
  const [posts, setPosts] = useState([]);
  const [WidthSet, setWidthSet] = useState(window.innerWidth);
  const [pushMessageToSnackbar, setPushMessageToSnackbar] = useState('');
  const [isDeletePostDialogOpen, setIsDeletePostDialogOpen] = useState(false);
  const [isDeletePostDialogLoading, setIsDeletePostDialogLoading] = useState(
    false
  );
  const [persons, setPersons] = useState([]);
  const [flag, setFlag] = useState(true);
  const [deleteTarget, setDeleteTarget] = useState(null);

  var Albumbox = []

useEffect(()=>{
      if(flag){
          
    Axios({
        method: "POST",
        url: "https://start.azintranet.com:5000/api/g-album/get-albums",
        data: {
          department: id
        }
    }).then(res=>{
        console.log(res.data)
        const iterations = res.data.length;
        let i = 1;
          const oneDaySeconds = 60 * 60 * 24;
            let curUnix = Math.round(
            new Date().getTime() / 1000 - iterations * oneDaySeconds
          );
        res.data.map((album)=>{
            {
              if(album.department===id){
              console.log("good")
              Albumbox.push({
                  id: i,
                department: album.department,
                iden: album._id,
                title: album.headline,
                date: album.createdAt,
                description: album.summary,
                src: 'https://start.azintranet.com:5000/'+album.image,
                name: album.author,
                timestamp: curUnix,
                imageText: 'Image Text',
              })
            }
            i++
        }})
          setFlag(false);
          setPosts(Albumbox)
          curUnix += oneDaySeconds;
    })
    }
},[setPosts])

  console.log(persons.length)

  const history = useHistory();

  useEffect(()=>{
    window.addEventListener('resize', function() {
      setWidthSet(window.innerWidth)
    });
  })
 

  const getPushMessageFromChild = useCallback(
    (pushMessage) => {
      setPushMessageToSnackbar(() => pushMessage);
    },
    [setPushMessageToSnackbar]
  );

  const closeDeletePostDialog = useCallback(() => {
    setIsDeletePostDialogOpen(false);
    setIsDeletePostDialogLoading(false);
  }, [setIsDeletePostDialogOpen, setIsDeletePostDialogLoading]);


  const deletePost = useCallback(() => {
    setIsDeletePostDialogLoading(true);
    console.log(deleteTarget)
    setTimeout(() => {
      const _posts = [...posts];
      const index = _posts.findIndex(
        (element) => element.iden === deleteTarget.iden
      );
      _posts.splice(index, 1);
      console.log(index)
      setPosts(_posts);

    Axios({
        method: "DELETE",
        url: "https://start.azintranet.com:5000/api/g-album/delete-album/"+deleteTarget.iden
      }).then(res=>{
            pushMessageToSnackbar({
            text: "Your post has been deleted",
            });
            closeDeletePostDialog();
      })

    }, 1500);
  }, [
    posts,
    setPosts,
    setIsDeletePostDialogLoading,
    pushMessageToSnackbar,
    closeDeletePostDialog,
    deleteTarget
  ]);

  const onDelete = useCallback((post) => {
    
    setIsDeletePostDialogOpen(true);
    setDeleteTarget(post)
    console.log(post)
  }, [setIsDeletePostDialogOpen, setDeleteTarget]);

  const handleChangePage = useCallback(
    (__, page) => {
      setPage(page);
    },
    [setPage]
  );

  const onOpen = useCallback((post)=>{
      history.push(`/group/287/${id}/album/${post.iden}`)
  })

//   const fetchRandomPosts = useCallback(() => {
//     shuffle(persons);
//     const posts = [];
//     const iterations = persons.length;
//     console.log(iterations)
//     const oneDaySeconds = 60 * 60 * 24;
//     let curUnix = Math.round(
//       new Date().getTime() / 1000 - iterations * oneDaySeconds
//     );
//     for (let i = 0; i < iterations; i += 1) {
//       const person = persons[i];
//       const post = {
//         id: i,
//         src: person.cover,
//         timestamp: curUnix,
//         name: person.author,
//       };
//       curUnix += oneDaySeconds;
//       posts.push(post);
//     }
//     posts.reverse();
//     setPosts(posts);
//   }, [setPosts]);

  const Add_album =(e)=>{
    e.preventDefault();
    history.push(`/group/287/${id}/create-album`)
}

    // useEffect(() => {
    //     fetchRandomPosts();
    // }, [
    //     fetchRandomPosts,
    // ]);

  const printImageGrid = useCallback(() => {
    if (posts.length > 0) {
      return (
          <div>
            <Sidebar id = {id}/>
            <div className = {WidthSet >= 1280 ? classes.Article_container_desktop : classes.Article_container_mobile}>
                <Box p={1}>
                        <ConsecutiveSnackbarMessages
                        getPushMessageFromChild={getPushMessageFromChild}
                    />
                    <Grid container spacing={1}>
                    {posts
                        .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                        .map((post) => (
                        <Grid item xs={6} sm={4} md={3} key={post.id}>
                            <SelfAligningImage
                            src={post.src}
                            title={post.name}
                            timeStamp={post.timestamp}
                            options={[
                                {
                                name: "Delete",
                                onClick: () => {
                                    onDelete(post);
                                },
                                icon: <DeleteIcon />
                                },
                                {
                                name: "Open",
                                onClick: () => {
                                    onOpen(post);
                                },
                                icon: <LaunchRoundedIcon />,
                                }
                            ]}
                            />
                        </Grid>
                        ))}
                    </Grid>
                </Box>
            </div>
        </div>
      );
    }
    else{
    return (
      <div>
        <Sidebar id = {id}/>
        <Box m={2} className = {WidthSet >= 1280 ? classes.Article_container_desktop : classes.Article_container_mobile}>
          <HighlightedInformation>
            No photo added yet. Click on &quot;NEW&quot; to create your first one.
          </HighlightedInformation>
        </Box>
      </div>
    );
  }
  }, [posts, onDelete, page]);

  return (
    <Paper >
      <Toolbar className={classes.toolbar}>
        <Typography variant="h6">All album</Typography>
        <Button
          style = {{margin: "auto"}}
          variant="contained"
          color="secondary"
          onClick={Add_album}
          disableElevation
        >
          Add Album
        </Button>
      </Toolbar>
      <Divider />
      {printImageGrid()}
      <TablePagination
        className = {WidthSet >= 1280 ? classes.Article_container_desktop : classes.Article_container_mobile}
        component="div"
        count={posts.length}
        rowsPerPage={rowsPerPage}
        page={page}
        backIconButtonProps={{
          "aria-label": "Previous Page",
        }}
        nextIconButtonProps={{
          "aria-label": "Next Page",
        }}
        onChangePage={handleChangePage}
        classes={{
          select: classes.dNone,
          selectIcon: classes.dNone,
          actions: posts.length > 0 ? classes.dBlock : classes.dNone,
          caption: posts.length > 0 ? classes.dBlock : classes.dNone,
        }}
        labelRowsPerPage=""
      />
      <ConfirmationDialog
        open={isDeletePostDialogOpen}
        title="Confirmation"
        content="Do you really want to delete the post?"
        onClose={closeDeletePostDialog}
        loading={isDeletePostDialogLoading}
        onConfirm={deletePost}
      />
    </Paper>
  );
}

Album.propTypes = {
  openAddPostModal: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
  posts: PropTypes.arrayOf(PropTypes.object).isRequired,
  setPosts: PropTypes.func.isRequired,
  pushMessageToSnackbar: PropTypes.func,
};

export default withStyles(styles)(Album);
